import React, { FC } from "react";

import IconButton from "../Button/IconButton";
import { IconName } from "../Icon/Icon";
import { VisuallyHidden } from "../VisuallyHidden";

import {
	alertBannerColors,
	AlertBannerIcon,
	ChildrenWrapper,
	DismissButtonWrapper,
	IconAndChildrenWrapper,
	StyledAlertBanner,
} from "./AlertBanner.styles";
import { AlertBannerProps, AlertBannerVariants } from "./AlertBanner.types";

export const alertBannerIcons: Record<AlertBannerVariants, IconName> = {
	information: "Info",
	warning: "TriangleAlert",
	error: "OctagonAlert",
	success: "Check",
};

const AlertBanner: FC<AlertBannerProps> = ({
	children,
	onDismiss,
	dataQA,
	role = "alert",
	variant = "information",
	...props
}) => (
	<StyledAlertBanner variant={variant} data-qa={dataQA} role={role} {...props}>
		<VisuallyHidden
			data-qa={`${dataQA}-visually-hidden`}
		>
			{/* screen reader should announce info not information */}
			{variant === "information" ? "info" : variant}
		</VisuallyHidden>
		<IconAndChildrenWrapper data-qa={`${dataQA}-icon-and-children-wrapper`}>
			<AlertBannerIcon
				width="20"
				height="20"
				name={alertBannerIcons[variant]}
				color={alertBannerColors[variant]}
				aria-hidden="true"
			/>
			<ChildrenWrapper data-qa={`${dataQA}-children-wrapper`}>{children}</ChildrenWrapper>
		</IconAndChildrenWrapper>
		{onDismiss
			&& (
				<DismissButtonWrapper>
					<IconButton icon="CircleX" dataQA={`${dataQA}-dismiss-button`} variant="tertiary" onClick={onDismiss} />
				</DismissButtonWrapper>
			)}
	</StyledAlertBanner>
);

AlertBanner.displayName = "AlertBanner";

export default AlertBanner;
